.hero {
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  padding-bottom: 2em;
  text-align: center;
  font-family: SpaceGrotesk, sans-serif;
}

.navigation {
  margin-top: 1em;
  color: #fff;
}

.logo {
  margin: 1em;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.33px;
  text-decoration: none;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
  margin: 1em;
  list-style: none;
  cursor: pointer;
}

.profile-img {
  min-height: 383px;
  background-image: url(image-profile-mobile.webp);
  background-position: center;
  background-size: cover;
  z-index: 3;
}

.hero-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
  padding: 1.2em;
}

.hero-title {
  max-width: 13ch;
  margin-top: 0.5em;
  color: #fff;
  font-size: 40px;
  line-height: 40px;
  letter-spacing: -1.14px;
}

.hero-title span {
  -webkit-text-decoration: underline 4px #4EE0A0;
          text-decoration: underline 4px #4EE0A0;
  text-underline-offset: 10px;
  position: relative;
}

.hero-para {
  margin-top: 0.5em;
  color: #d9d9d9;
  font-weight: 200;
}

.hero-contact {
  position: relative;
  color: #fff;
  font-size: 16px;
  letter-spacing: 2.29px;
  line-height: 26px;
  font-weight: 200;
  -webkit-text-decoration: 2px solid underline #4EE0A0;
          text-decoration: 2px solid underline #4EE0A0;
  text-underline-offset: 10px;
  text-transform: uppercase;
}

.circle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -64px;
  width: 129px;
  height: 129px;
  border-radius: 50%;
  border: 1px solid #fff;
}

.rings-hero {
  position: absolute;
  top: 18%;
  left: -350px;
}

@media screen and (max-width: 290px) {
  .hero-title {
    font-size: 35px;
    line-height: 50px;
  }
}
@media screen and (min-width: 768px) {
  .hero {
    display: grid;
    grid-template-columns: repeat(5, minmax(150px, 254px));
    justify-content: center;
  }
  .navigation {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    grid-row: 1/2;
    display: flex;
    justify-content: space-between;
  }
  .logo {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -0.44px;
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .socials {
    grid-column: 5/6;
  }
  .profile-img {
    background-image: url(image-profile-tablet.webp);
    background-size: cover;
    height: 600px;
    grid-column: 4/6;
    grid-row: 2/3;
  }
  .hero-text {
    height: 100%;
    grid-column: 1/6;
    grid-row: 2/3;
    text-align: start;
    align-items: flex-start;
    justify-content: flex-end;
    z-index: 5;
  }
  .hero-title {
    max-width: 60%;
    font-size: 72px;
    line-height: 72px;
    letter-spacing: -2.05px;
    margin-bottom: 0.5em;
    z-index: 4;
  }
  .hero-para {
    max-width: 70%;
    font-size: 18px;
    line-height: 28px;
  }
  .circle {
    position: absolute;
    top: 85%;
    right: -64px;
    width: 129px;
    height: 129px;
    border-radius: 50%;
    border: 1px solid #fff;
    z-index: 7;
  }
}
@media screen and (min-width: 1440px) {
  .hero {
    grid-template-columns: repeat(6, 168.3px);
    justify-content: center;
  }
  .hero-text {
    gap: 1em;
    padding-left: 0;
  }
  .profile-img {
    background-image: url(image-profile-desktop.webp);
    grid-column: 5/7;
  }
  .hero-text {
    grid-column: 1/5;
  }
  .hero-title {
    max-width: 100%;
    margin-bottom: 0;
    font-size: 88px;
    line-height: 88px;
    letter-spacing: -2.5px;
  }
  .hero-title span {
    text-underline-offset: 22px;
  }
  .hero-para {
    font-size: 18px;
    line-height: 28px;
  }
  .navigation {
    left: 50%;
    transform: translateX(-50%);
    max-width: 1010px;
  }
  .circle {
    position: absolute;
    top: 82.5%;
    right: 36.5%;
    width: 129px;
    height: 129px;
    border-radius: 50%;
    border: 1px solid #fff;
    z-index: 7;
  }
  .rings-hero {
    top: 12%;
    left: -15%;
  }
  .logo {
    margin-left: 0;
  }
  .socials {
    margin-right: 0;
  }
}
@media screen and (min-width: 1900px) {
  .hero {
    max-height: 60vh;
    display: grid;
    grid-template-columns: repeat(5, 260px);
  }
  .profile-img {
    grid-column: 4/6;
    grid-row: 1/4;
  }
  .hero-text {
    grid-column: 1/4;
  }
  .hero-para {
    max-width: 60ch;
  }
  .circle {
    left: 52.75%;
  }
}/*# sourceMappingURL=nav.css.map */